@import url('https://fonts.googleapis.com/css2?family=Poiret+One&display=swap');
body{

    font-family: 'Poiret One', cursive;

    font-weight: bold;
  
    display: flex;
 flex-direction: column;
 height: 100vh;
    
}
.navbar{
    background-color: #fff !important;
}
.navbar-brand{
    color: #3498db !important;
    font-size: 1.9rem !important;
}

.navbar a{
    font-size:  1.3rem;
    text-transform: capitalize;
    color: white !important;
}
.menu_active{
    font-weight: bold;
    border-bottom: 1px solid #565387;
}
.navbar a:hover{
    color: crimson !important;
}

#header{
    width: 100%;

    height: 80vh;
    margin-top: 60px;
}

#header h1{
    margin: 0 0 10px 0;
    
    font-size: 48px;
    font-weight: 700;
    color: black;
}
#header .brand_name{
    color: #3498db;
}
span{
    color: rgb(215, 23, 12);
}
#header h2{
    color: #484848;
    font-size: 24px;
}
#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;


}
#header .btn-get-started:hover{
    background-color: #3498db;
    color: #fff;
}

.header_img{
    text-align: right;

}
#header .animate{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
@keyframes up-down{
    0%{
        transform:  translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
footer{
  
    width: 100%;
    background-color: #3498db;
    margin-top: auto;
  
    
    
}
footer p{
line-height: 40px;
    color: white;
}
.fa-instagram{
   
    font-size: 32px;
}
.fa-whatsapp{
   
    font-weight: bold;
    font-size: 25px;
}
.fa-twitter{
    font-size: 25px;
}
.card-title{
font-weight: bold;
color: crimson;
}
.bg{
    background-color: #3498db !important;
    color: white;
}